import React,{ useState,useEffect} from 'react';
import "./path.css";
import PaperImage from "./paper.png";
import {GiStarFlag} from "react-icons/gi"
import Datestr from './date/date';
import data from "./data"

function useForceUpdate(){
  const [value, setValue] = useState(0); // integer state
  return () => setValue(value => value + 1); // update the state to force render
}

const LearningPath = () => {
  const [Circles, setCircles] = useState([]);
  const [achievements, setachievements] = useState([]);
  const right = [1,2,5,6]
  const forceUpdate =useForceUpdate()

  useEffect(() => {
    let factor = window.innerWidth<=725?-10:0
    let paths = [
      Path(95+factor, 20),
      Path(95+factor,10),
  
      Path(0 ,40),
      Path(0 ,30),
  
      Path(95 + factor, 60),
      Path(95 + factor,50),
  
      Path(0 ,80),
      Path(0 ,70),
    ];

    paths.forEach((p,i)=>{
      p.title=""
      p.active = false;
      p.title = data[i].title
      p.date = data[i].date
      p.img=data[i].img
      p.detail = data[i].detail

    })
    setachievements(paths)

    setInterval(forceUpdate,10)
    let plane = document.getElementById("paper-plane")
    plane.addEventListener("animationstart",()=>console.log("animationstart"));
    let parent =plane.offsetParent
    let rect = plane.getBoundingClientRect()
    
    let y = ( rect.y-parent.getBoundingClientRect().y)+(rect.height/2)
    let x = plane.getBoundingClientRect().x +(rect.width/2)

    let first={
      x:x,
      y:y,
      r:2
    }
    setCircles([first])
    window.onscroll=()=>{
      rect = plane.getBoundingClientRect()
      y = ( rect.y-parent.getBoundingClientRect().y) + rect.height/2
      x = plane.getBoundingClientRect().x +(rect.width/2)
      let f = Circles.filter((c)=>c.x<=x+7 && c.x>=x-7 && c.y<=y+7 && c.y>=y-7)
      if(f.length===0 && window.innerWidth>=725){
        let c = Circles
        c.push({
          x:x,
          y:y,
          r:2
        })
        setCircles(c)
      }


      let achie = achievements.length===0?paths:achievements
      let fFactor = window.innerWidth/24
      achie.forEach((c)=>{
        if(c.x<=x+fFactor &&  c.y<=y+fFactor ){
          c.active=true

        }
      })
      setachievements(achie)
    }


    
  }, []);

  function Path(x, y){
    var xpercent = window.innerWidth / 100;
    var h = document.getElementsByClassName("paper-plane-animation")[0].offsetHeight
    var ypercent =h/100
  
    return {
      x: x * xpercent,
      y: y *ypercent,
      opacity: 1,
    };
  };
  const get_right=(x)=>window.innerWidth-x
  return (
    <div id="trigger-element" className="paper-plane-animation">
      <img
        id={"paper-plane"}
        className="paper-plane"
        src={PaperImage}
        alt="paperplane"
      />


      <svg className="circle-svg" height={"100vh"} width={"100vw"}>
        {Circles.map((circle)=>{
            return <circle cx={circle.x} cy={circle.y} r={circle.r}/>
          })}
      </svg>
      <>
          {achievements.map((a,i)=>{
            
            
            return(
              <>
                
                
                  
                  {right.includes(i+1)?<>
                    <div className={a.active?"flag active flag" + String(i):"flag flag" + String(i)} style={{position:"absolute",top:String(a.y )+"px",right:String(0)+"px"}}>
                      <Datestr date={a.date} /><GiStarFlag />
                    </div>
                    
                    </>:<>
                    <div className={a.active?"flag active flag" + String(i):"flag flag" + String(i)} style={{position:"absolute",top:String(a.y )+"px",left:String(0)+"px"}}>
                      <GiStarFlag /><Datestr date={a.date} />
                    </div>
                    
                    </>}

              </>
            )
          })}
      </>

    </div>
  );
};

export default LearningPath;

